import { keyframes } from '@emotion/core';
import styled from '@emotion/styled';
import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { isEmail, isEmpty, isURL } from 'validator';
import axios from '../../utils/axios';
import { media, rem, xss } from '../../utils/helper';
import marked from '../../utils/marked';

const bounce = keyframes`
  from, 20%, 53%, 80%, to {
    transform: translate3d(0,0,0);
    border-bottom: 1px solid #f90000
  }

  40%, 43% {
    transform: translate3d(0, -30px, 0);
  }

  70% {
    transform: translate3d(0, -15px, 0);
  }

  90% {
    transform: translate3d(0,-4px,0);
  }
`;

const CommentFormWrap = styled.form`
    display: flex;
    flex-direction: column;
    border: 1px solid #f0f0f0;
    border-radius: 4px;
    padding: 10px;
`;

const FormGroup = styled.div`
    display: flex;
    flex: 1 0 auto;
    margin-right: 5px;
    margin-bottom: 10px;
    border-radius: 2px;
    flex-wrap: wrap;
`;

const FormInput = styled.input`
    color: #333;
    flex: 1 0 auto;
    margin-left: -1px;
    font-size: 13px;
    padding: 3px 0;
    height: 36px;
    min-width: 200px;
    box-sizing: border-box;
    border: none;
    border-bottom: 1px dashed #dedede;
    padding-left: 5px;
    width: 33.33%;
    transition: all .4s ease;
    ${(props: any) => props.isError ? { animation: `${bounce} 1s ease infinite` } : ''};
    &:focus {
        outline: none;
        border-bottom-color: #eb5055;
    };
    &:-webkit-autofill {
        -webkit-box-shadow: 0 0 0px 1000px #ffffff inset !important;
         -webkit-text-fill-color: #3E3E3E !important;
    };
    ${media.phone`
        width: 100%;
    `}
`;

const ContentWrap = styled.div((_) => ({
    marginBottom: '6px'
}));

const Textarea = styled.textarea`
    width: 100%;
    min-width: 200px;
    min-height: 90px;
    border: none;
    box-sizing: border-box;
    border-bottom: 1px dashed #dedede;
    ${(props: any) => props.isError ? { animation: `${bounce} 1s ease infinite` } : ''};
    &:focus {
        outline: none;
    }
`;

const PreviewPane = styled.div`
    width: 100%;
    min-width: 200px;
    min-height: 90px;
    max-height: 90px;
    border: none;
    box-sizing: border-box;
    border-bottom: 1px dashed #dedede;
    overflow: auto;
    img {
        max-width: 20px;
        vertical-align: text-bottom;
    }
`;

const Footer = styled.div`
    transition: all .4s ease-in;
`;

const ButtonSubmitWrap = styled.div`
    line-height: 46px;
    color: #999;
    font-size: 12px;
    margin-left: 6px;
    ${media.phone`
        span {
            display: block;
        }
    `}
`;

const ButtonSubmit = styled.button`
    color: #fff;
    cursor: pointer;
    font-size: 12px;
    font-weight: 400;
    padding: 8px 12px;
    text-align: center;
    border: 1px solid #6190e8;
    border-radius: 5px;
    background-color: #6190e8;
    margin-top: 10px;
    float: right;
`;

const PreviewButton = styled.button`
    color: #fff;
    cursor: pointer;
    font-size: 12px;
    font-weight: 400;
    padding: 8px 12px;
    text-align: center;
    border: 1px solid #19be6b;
    background-color: #19be6b;
    border-radius: 5px;
    margin-top: 10px;
    float: right;
    margin-right: 10px;
    &:focus {
        outline: none;
    }
`;

const ErrorTipDiv = styled.p((_) => ({
    color: '#721c24',
    textAlign: 'center',
    margin: '0 0 10px 0',
    backgroundColor: '#f8d7da',
    padding: '8px 0',
    borderRadius: '4px',
    border: '1px solid #f5c6cb'
}));

const EmotionWrap = styled.div`
    position: relative;
`;

const EmoticonList = styled.ul`
    user-select: none;
    padding: 0;
    margin: 0;
    border-bottom: 1px dashed #dedede;
    font-size: 0;
`;

const EmoticonLi = styled.li`
    list-style-type: none;
    padding: 5px 8px;
    border-radius: 5px;
    display: inline-block;
    font-size: 12px;
    line-height: 14px;
    cursor: pointer;
    >img {
        display: block;
        margin: 0;
        max-width: 30px;
    }
`;

interface Props {
    url: string;
    replyId?: string;
    articleId?: string;
}

