import styled from '@emotion/styled';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { State } from '../../redux/reducers/links';
import { media, rem } from '../../utils/helper';
import LinkItem from './item';

const FriendlyLinksWrap = styled.div`
    background-color: #fff;
    display: flex;
    flex: 1 0 auto;
    flex-direction: column;
    padding: ${rem('28px')};
`;

const Header = styled.header((_) => ({
    textAlign: 'center'
}));

const HeaderTitle = styled.h2((_) => ({
    fontSize: '24px',
    margin: '5px'
}));

const BodyUl = styled.ul((_) => ({
    display: 'flex',
    flexWrap: 'wrap',
    padding: 0,
    margin: 0
}));

const TIP = styled.h3((_) => ({
    color: '#333',
    fontSize: '14px',
    padding: '5px 5px 5px 20px',
    '>i': {
        marginRight: '5px',
    }
}));

const HR = styled.hr((_) => ({
    margin: '20px 0',
    height: '3px',
    border: 'none',
    backgroundColor: '#ccc',
    backgroundImage: 'repeating-linear-gradient(-45deg, #fff, #fff 4px, transparent 4px, transparent 8px)'
}));

const LI = styled.li((_) => ({
    fontSize: '14px',
    listStyle: 'circle',
    padding: '5px'
}));

const Jump = styled(Link)((_) => ({
    fontSize: '14px',
    color: '#f51c1c',
    textDecoration: 'none'
}));

class FriendlyLinks extends Component<any, any> {
    public render() {
        const { links } = this.props._DB;
        return (
            <FriendlyLinksWrap>
                <Header>
                    <HeaderTitle>----</HeaderTitle>
                    <p>Web</p>
                </Header>
                <BodyUl>
                    {
                        links.map((item: any) => (
                            <LinkItem item={item} key={item._id}></LinkItem>
                        ))
                    }
                </BodyUl>
                <footer className="FriendlyLinks-footer">
                    <HR />
                    <TIP><i className="fa fa-info-circle fa-fw"></i></TIP>
                    <ol>
                        <LI ></LI>
                        <LI ></LI>
                        <LI ></LI>
                        <LI ></LI>
                    </ol>
                    <HR />
                    <TIP className="FriendlyLinks-tip"><i className="fa fa-map fa-fw"></i></TIP>
                    <ol>
                        <LI><strong></strong></LI>
                        <LI><strong></strong>GitHub</LI>
                        <LI><strong></strong>80</LI>
                        <LI><strong></strong>logo.png.jpg</LI>
                    </ol>
                    <HR />
                    <TIP className="FriendlyLinks-tip"><i className="fa fa-hand-o-right fa-fw"></i></TIP>
                    <ol>
                        <LI>
                            
                            <Jump to="/blog/guestbook" className="FriendlyLinks-jump">
                                <strong></strong>
                            </Jump>
                            
                        </LI>
                    </ol>
                </footer>
            </FriendlyLinksWrap>
        );
    }
}

export default connect(
    (state: State) => ({
        _DB: state.links
    })
)(FriendlyLinks as any);